@font-face {
  font-family: 'icomoon';
  src:  url('./icomoon.eot?1ccv9u');
  src:  url('./icomoon.eot?1ccv9u#iefix') format('embedded-opentype'),
    url('./icomoon.ttf?1ccv9u') format('truetype'),
    url('./icomoon.woff?1ccv9u') format('woff'),
    url('./icomoon.svg?1ccv9u#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change . */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-plus:before {
  content: "\ea0a";
}
